<template>
  <div>
    <el-upload
      class="avatar-uploader"
      action=""
      :auto-upload="false"
      :file-list="files"
      :on-change="changeImg"
      :on-remove="removeImg"
      :on-exceed="overImgs"
      :limit="limitImg"
      list-type="picture-card"
      :before-upload="beforeUpload">
      <i class="el-icon-plus"></i>
    </el-upload>
  </div>
</template>

<script>
export default {
    props:{
      limitImg: {
        type: Number  //
      },
      files:{
        type: Array  //
      }
    },
    data () {
      return {
        
      }
    },
    methods:{
      //
      changeImg(file, fileList){
        this.$stamp(null,fileList)
        this.$emit('imgFiles',fileList)
      },
      //
      removeImg(file,fileList){
        this.$stamp(null,fileList)
        this.$emit('imgFiles',fileList)
      },
      //
      beforeUpload(file){
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      //
      overImgs(){
        this.$message.error(`${this.limitImg}`)
      }
    },
    computed: {
      
    },
    components: {
      
    },
    created(){
      
    }
}
</script>

<style>
</style>



